.indexLink {
	width: 100%;
	height: 100%;
	background-color: $color-blue;
	& .containers {
		@include flexStyle(flex-start, center, row, 14px);
		height: 100%;
		overflow: hidden;
	}
	&__content {
		width: 50%;
		padding: 70px 0 90px 0;
		@include flexStyle(flex-start, flex-start, column, 30px);
		&-title {
			text-transform: uppercase;
			@include fontStyle(32px, 700, 58px, $color-white);
			& span {
				color: $color-yellow;
			}
		}
		&-subtitle {
			letter-spacing: -0.5px;
			@include fontStyle(20px, 400, 32px, $color-white);
			& span {
				font-weight: 700;
			}
		}
		&-button {
			margin-top: 30px;
		}
	}
	&__banner {
		width: 50%;
		height: 100%;
		& img {
			@include imgFullSize(auto);
		}
	}
}

//| Media

@media (max-width: 1100px) {
	.indexLink .containers {
		@include flexStyle(flex-start, flex-start, column);
	}
	.indexLink__content {
		width: 100%;
	}
	.indexLink__banner {
		width: calc(100% + 34px);
		margin-left: -17px;
		& img {
			width: 100%;
		}
	}
}
